.italic {
  font-style: italic;
}

.normal {
  font-style: normal;
}

.oblique {
  font-style: oblique;
}

.lighter {
  font-weight: lighter
}

.regular {
  font-weight: $--regular
}

.bold {
  font-weight: $--bold
}

.line-through {
  text-decoration: line-through;
}

.overline {
  text-decoration: overline;
}

.underline {
  text-decoration: underline;
}

.text-capitalize {
  text-transform: capitalize;
}

.text-lowercase {
  text-transform: lowercase;
}

.text-uppercase {
  text-transform: uppercase;
}

.pre {
  white-space: pre;
}

.pre-line {
  white-space: pre-line;
}

.pre-wrap {
  white-space: pre-wrap;
}


/*-----------------   css text    --------------------*/

.text-break {
  word-break: break-all !important;
  word-wrap: break-word !important;
}

.text-unbreak {
  white-space: nowrap !important;
  word-wrap: normal !important;
  overflow: hidden;
}

.text-undecorate {
  text-decoration: none !important;
}

.text-underline {
  text-decoration: underline !important;
}

.text-through {
  text-decoration: line-through!important;
}

.text-sub {
  vertical-align: sub!important;
  /*下标字*/
}

.text-super {
  vertical-align: super!important;
  /*上标字*/
}

.text-indent,
.text-indent p,
.text-indent div {
  text-indent: 2em;
  /*缩进*/
}


/*text wrap*/

.text-nowrap {
  @include text-overflow()
}

.text-nowrap-1 {
  @include text-line-clamp($font-size-base, $line-height-base, 1)
}

.text-nowrap-2 {
  @include text-line-clamp($font-size-base, $line-height-base, 2)
}

.text-nowrap-3 {
  @include text-line-clamp($font-size-base, $line-height-base, 3)
}

.text-nowrap-4 {
  @include text-line-clamp($font-size-base, $line-height-base, 4)
}

.text-nowrap-5 {
  @include text-line-clamp($font-size-base, $line-height-base, 5)
}


/*----------------------------------------------------
 * text line height
 *---------------------------------------------------*/

.lh-1 {
  line-height: $--line-height-1;
}

.lh-2 {
  line-height: $--line-height-2;
}

.lh-3 {
  line-height: $--line-height-3;
}

.lh-4 {
  line-height: $--line-height-4;
}

.lh-5 {
  line-height: $--line-height-5;
}


/*----------------------------------------------------
 * text font size
 *---------------------------------------------------*/

.fs {
  font-size: $font-size-base
}

.fs-1 {
  font-size: $--font-size-1
}

.fs-2 {
  font-size: $--font-size-2
}

.fs-3 {
  font-size: $--font-size-3
}

.fs-4 {
  font-size: $--font-size-4
}

.fs-5 {
  font-size: $--font-size-5
}

.fs-small {
  font-size: $--font-size-small
}

.fs-big {
  font-size: $--font-size-big
}

.fs-large {
  font-size: $--font-size-large
}

/*----------------------------------------------------
 * text align
 *---------------------------------------------------*/

.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-justify {
  text-align: justify;
}


/*----------------------------------------------------
 * active
 *---------------------------------------------------*/

.text-active,
.active > .text,
.active > .auto .text,
.collapsed > .text,
.collapsed > .auto .text {
  display: none !important;
}

.active > .text-active,
.active > .auto .text-active,
.collapsed > .text-active,
.collapsed > .auto .text-active {
  display: inline-block !important;
}


/*
 * Component: blockquote
 * ----------------
 */

.blockquote {
  position: relative;
  background-color: #f9f9f9;
  padding: 15px 40px;
  font-size: 18px;
  line-height: 28px;
  color: #777;
  &:before,
  &:after {
    // font-family: Arial, Helvetica, sans-serif;
    background: none;
    width: 0px;
    height: 0px;
    font-style: normal;
    color: #ccc;
    font-size: 80px;
    position: absolute;
  }
  &:before {
    content: '\201c';
    top: 30px;
    left: 10px;
  }
  &:after {
    content: '\201d';
    bottom: 10px;
    right: 35px;
  }
}
